<template>
    <div class="header">
        城市选择
        <router-link to="/">
        <div class="iconfont header-icon">&#xe624;</div>
        </router-link>
    </div>
</template>

<script>
    export default {
        name:"CityHeader"
    }
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.header
    position :relative
    overflow :hidden
    height :$headerHeight
    line-height :$headerHeight
    color:#fff
    text-align :center
    background :$bgColor
    font-size:.32rem
    .header-icon
        position :absolute
        top :0px
        left :0
        width:0.64rem
        text-align :center
        font-size :.4rem
        color :#fff
</style>


